.search{
   height: 50px;
   padding: 10px;
   display: flex;
   justify-content: space-between;
   align-items: center;
   
}
.searchBox{
    height: 20px;
    background-color: rgb(232, 236, 255);
    display: flex;
    align-items: center;
    padding: 15px;
    border-radius: 50px;
    justify-content: space-between;
    width: 68%;
}
.searchBoxActive {
  background-color: white;
}
.searchInput{
    border: none;
    outline: none;
    background-color: rgb(232, 236, 255);
    font-size: 18px;
    width:650px;
    padding-left: 5px;
}

@media only screen and (max-width: 375px) {
    .searchInput{
        border: none;
        outline: none;
        background-color: rgb(232, 236, 255);
        font-size: 18px;
        width:100px;
        padding-left: 5px;
    }
  }
  @media screen and (min-width: 375px) and (max-width: 576px) {
    .searchInput{
        border: none;
        outline: none;
        background-color: rgb(232, 236, 255);
        font-size: 18px;
        width:150px;
        padding-left: 5px;
    }
  }
  

  @media screen and (min-width: 576px) and (max-width: 768px) {
    .searchInput{
        border: none;
        outline: none;
        background-color: rgb(232, 236, 255);
        font-size: 18px;
        width:250px;
        padding-left: 5px;
    }
  }

  @media screen and (min-width: 768px) and (max-width: 992px) {
    .searchInput{
        border: none;
        outline: none;
        background-color: rgb(232, 236, 255);
        font-size: 18px;
        width:400px;
        padding-left: 5px;
    }
  }

  @media screen and (min-width: 992px) and (max-width: 1200px) {
    .searchInput{
        border: none;
        outline: none;
        background-color: rgb(232, 236, 255);
        font-size: 18px;
        width:500px;
        padding-left: 5px;
    }
  }

.searchInput:focus {
    background-color: white;
}
.SearchIcons{
    display: flex;
    gap: 10px;
    align-items: center;
}
.supIconsInti,.setIconsInti,.gooIconsInti,.proIconsInti{
    height: 30px;
    width: 30px;
    font-size: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.supIconsInti:hover,.setIconsInti:hover,.gooIconsInti:hover {
    background-color: rgb(229, 229, 229);
    border-radius: 50px;
}
.supText,.setText,.gooText{
    position: absolute;
    top:55px;
    font-size: 12px;
    height: 25px;
    width: 60px;
    background-color: gray;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    font-weight: 600;
    display: none;
}
.supIconsInti:hover .supText,
.setIconsInti:hover .setText,
.gooIconsInti:hover .gooText {
    display: flex;
}