.logoMain {
    width: 20%;
    height: 23vh;
    display: flex;
    flex-direction: column;
    gap: 20px;
}
.logo{
    display: flex;
}
.main{
    width: 80%;
}
.gmailLogo{
    width: 35px;
    height: 25px;
}
.iconWraper{
    display: flex;
   margin-left: 20px;
    gap: 20px;
    padding-top: 10px;
}
.gmail {
    font-size: 23px;
    font-weight: 500;
    color: rgb(105, 102, 102);
}
.hamburgerIcon{
   font-size: 20px;
   color: rgb(61, 56, 56);
}
.composeWrapper{
   display: flex;
   height: 60px;
   width: 150px;
   background-color: #c7ecff;
   justify-content: center;
   align-items: center;
   gap: 10px;
   border-radius: 15px;
   margin-left: 10px;
}
.composeIcon{
    font-size: 25px;
}
.compose{
    font-weight: 500;
}
.composeWrapper:hover{
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.3);
}
.mailContent{
    position: absolute;
    bottom: 0;
    right: 50px;
}
