    height: 40px;
    position: absolute;
    bottom: 10px;
    right: 100px;
    box-shadow: 0 0px 5px rgba(0, 0, 0.5, 0.6);
   border-top-right-radius: 10px;
   border-top-left-radius: 10px; 
}
