.optionsMainwraper {
  height: 75vh;
  overflow-y: scroll;
}
.LogotText,
.categoriesOptions,
.starredText,
.snoozedText,
.impoText,
.senText,
.draText,
.leText,
.leunText,
.recipText,
.woText,
.labunText {
  display: flex;
  align-items: center;
  gap: 20px;
  height: 37px;
  border-top-right-radius: 50px;
  border-bottom-right-radius: 50px;
  padding-left: 30px;
  cursor: pointer;
}
.LogotText:hover,
.categoriesOptions:hover,
.starredText:hover,
.snoozedText:hover,
.impoText:hover,
.senText:hover,
.draText:hover,
.leText:hover,
.leunText:hover,
.recipText:hover,
.woText:hover,
.labunText:hover {
  background-color: rgb(240, 238, 238);
}
.categoriesOptions {
  padding-left: 50px;
}
.inboxIcon {
  font-size: 17px;
  font-weight: 600;
}
.inboxtext {
  font-weight: 600;
  font-size: 14px;
}
.categoriesMain {
  display: flex;
  align-items: center;
}
.categoriesIcons {
  display: flex;
  gap: 23px;
  margin-left: 5px;
  align-items: center;
}
.catMoreIcon {
  margin-left: 10px;
}
.label {
  display: flex;
  align-items: center;
  gap: 140px;
  margin-top: 20px;
}
.labelText {
  margin-left: 30px;
  font-size: 18px;
  font-weight: 500;
}
.labelIocn {
  font-size: 23px;
  font-weight: 500;
}
::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}

::-webkit-scrollbar-thumb {
  background-color: rgb(190, 185, 185);
  border-radius: 10px;
}

::-webkit-scrollbar-track {
  background-color: white;
}
