.mailBoxMain{
    height: 80vh;
    width: 40vw;
    background-color: white;
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.3);
   border-top-right-radius: 8px;
   border-top-left-radius: 8px;
}
.mailIcons{
    height: 10px;
    width: 10px;
    cursor: pointer;
}
.newMessage{
    font-weight: 500;
    font-size: 14px;
    padding-left: 20px;
   
}
.mailBoxTopIcons{
    display: flex;
    gap: 10px;
    padding-right: 20px;
}
.mailBoxTop{
    display: flex;
    justify-content:space-between;
    padding-top: 10px;
    height: 37px;
    background-color:#f2fafe
}
.mailBoxTo{
    display: flex;
    align-items: center;
    padding: 15px;
    /* border-bottom: solid 1px rgb(217, 217, 217); */
    height: 14px;
}
.toInput{
    width: 20px;
}
.mailBoxToInput{
    width: 420px;
    border: none;
    outline: none;
}
.to:hover{
    text-decoration: underline;
}
.hrTag{
   border-color: rgb(250, 250, 250);
    margin-top: -5px;
    margin-right: 15px;
    margin-left: 15px;
    opacity: .5;
}
.textArea{
    margin: 10px;
    border: none;
    outline: none;
    padding-left: 5px;
}
.mailStyle{
    margin: 10px 15px 0px 15px;
    border-color: solid 1px black;
    box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, 0.3);
    width: 95%;
    height: 30px;
    display: flex;
    gap: 10px;
    align-items: center;
    justify-content: space-around;
    color: rgb(93, 90, 90);
}
.undoRedo{
    font-size: 22px;
    display: flex;
    justify-content: space-around;
    gap: 3px;
    align-items: center;
    color: rgb(93, 90, 90);
    cursor: pointer;

}
.redo{
    transform: scaleX(-1);
}
.sans{
    display: flex;
    gap: 10px;
    display: flex;
    cursor: pointer;
}
.textStyle{
    font-size: 19px; 
    display: flex;
    gap: 10px;
    align-items: center;
    cursor: pointer;
}
.textAlign{
    display: flex;
    gap: 10px;
    cursor: pointer;
}
.finalSection{
    height: 35px;
    margin: 10px;
}
.send{
    height: 35px;
    background-color: rgb(48, 48, 246);
    width: 50px;
    font-size: 14px;
    font-weight: 600;
    border-top-left-radius: 50px;
    border-bottom-left-radius: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    border-right-color: solid 2px black;
    cursor: pointer;
}
.downIcon{
    height: 35px;
    background-color: rgb(48, 48, 246);
    width: 50px;
    border-top-right-radius: 50px;
    border-bottom-right-radius: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    cursor: pointer;
}
.sentIcon{
    display: flex;
    cursor: pointer;
}
.finalSection{
    display: flex;
    align-items: center;
    gap: 10px;
    margin-top: 20px;
}
.finalsectionIcons{
    display: flex;
    align-items: center;
    gap: 15px;
    font-size: 20px;
}
.formatting{
    height: 25px;
    width: 25px;
    background-color: rgb(224, 219, 219);
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    cursor: pointer;
}
.trash{
    margin-left: 50px;
    cursor: pointer;
}
.formattingText{
    position: absolute;
    bottom: 50px;
    font-size: 13px;
    height: 20px;
    width: 130px;
    background-color: gray;
    color: white;
    text-align: center;
    border-radius: 5px;
    font-weight: 600;
    display: none;
    cursor: pointer;
}
.formatting:hover .formattingText{
    display: block;
}
.attachText{
    position: absolute;
    bottom: 50px;
    font-size: 13px;
    height: 20px;
    width: 100px;
    background-color: gray;
    color: white;
    text-align: center;
    border-radius: 5px;
    font-weight: 600;
    display: none;
    cursor: pointer;
}
.attachIcon:hover .attachText{
    display: block;
}
.insertText{
    position: absolute;
    bottom: 50px;
    font-size: 13px;
    height: 20px;
    width: 100px;
    background-color: gray;
    color: white;
    text-align: center;
    border-radius: 5px;
    font-weight: 600;
    display: none;
    cursor: pointer;
}
.insertIcon:hover .insertText{
    display: block;
}
.emojiText {
    position: absolute;
    bottom: 50px;
    font-size: 13px;
    height: 20px;
    width: 100px;
    background-color: gray;
    color: white;
    text-align: center;
    border-radius: 5px;
    font-weight: 600;
    display: none;
    cursor: pointer;
}
.emojiIcon:hover .emojiText{
    display: block;
}
.driveText,.confidentiolText{
    position: absolute;
    bottom: 50px;
    font-size: 13px;
    height: 20px;
    width: 150px;
    background-color: gray;
    color: white;
    text-align: center;
    border-radius: 5px;
    font-weight: 600;
    display: none;
    cursor: pointer;
}
.driveIcon:hover .driveText{
    display: block;
}
.photoText,.signatureText,.moreText,.discardText{
    position: absolute;
    bottom: 50px;
    font-size: 13px;
    height: 20px;
    width: 100px;
    background-color: gray;
    color: white;
    text-align: center;
    border-radius: 5px;
    font-weight: 600;
    display: none;
    cursor: pointer;
}
.photoIcon:hover .photoText{
    display: block;
}
.confidentiolIcon:hover .confidentiolText{
    display: block;
}
.signatureIcon:hover .signatureText{
    display: block;
}
.moreIcon:hover .moreText{
    display: block;
}
.trash:hover .discardText{
    display: block;
    right: 0px;
}
.attachIcon,.attachText,.insertIcon,.emojiIcon,.driveIcon,.photoIcon,.confidentiolIcon,.signatureIcon,.moreIcon{
    cursor: pointer;
}

