.topSection{
    height: 50px;
    display: flex;
    gap: 20px;
}
.primary,.promotion,.social{
    height: 50px;
    width: 25%;
    display: flex;
    gap: 15px;
    align-items: center;
}
.primary{
    color:blue;
   padding-left: 40px;
   border-bottom: solid 2px blue;
}
.mailFirst{
    width:25%;
    display: flex;
    gap: 10px;
    height: 40px;
    align-items: center;
    padding-left: 40px;
    font-size: 15px;
    color: rgb(167, 160, 160) ;
}
.mailContentw {
    width: 60%;
    overflow: hidden;
    white-space: nowrap; 
    text-overflow: ellipsis; 
  }
  
.dateAndTime{
    width:15%;
    font-weight: bold;
    font-size: 12px;
    
}
.mailWrapper{
    height: 40px;
    display: flex;
    gap: 50px;
    align-items: center;
    border-bottom: solid 1px rgb(236, 231, 231);
    cursor: pointer;
}
.sender{
    font-weight: 650;
    color: black;
    width:70%;
    overflow: hidden;
    white-space: nowrap; 
    text-overflow: ellipsis; 
    font-size: 14px;
}
.mailWrapper:hover{
    box-shadow: 0 0px 5px rgba(0, 0, 0, 0.3);
}
.mailWrapper:hover .mailFirst{
    color: rgb(49, 43, 43);
}
.fillBackground {
   color: rgb(164, 164, 4);
}